export const AndOr = (): FormlyFieldConfig[] => {
  return [{
    key: 'operator',
    type: 'select-custom',
    className: 'flex',
    defaultValue: '&&',
    props: {
      viewValue: 'name',
      labelProp: 'name',
      valueProp: 'id',
      required: true,
      options: [
        {
          id: "&&",
          name: 'V'
        },
        {
          id: "||",
          name: 'Hoc'
        }
      ]
    },
    expressions: {
      'props.disabled': 'formState.disabled',
    },
  }]
}

