import { FormControl, FormGroup, ValidationErrors } from '@angular/forms';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { FormlyFieldConfig } from '@ngx-formly/core';
import * as dayjs from 'dayjs';
import { map } from 'rxjs';

export const FormlyConfigForm = (
  form: FormGroup,
  campaignService: IComLibsServicesProjectCampaignV3Service
): FormlyFieldConfig[] => {
  return [
    {
      key: 'name',
      type: 'input',
      className: '',
      props: {
        placeholder: 'Nhp tn chin dch',
        label: 'Tn chin dch',
        type: 'string',
        required: true,

      },
      validators: {
        validation: [{ name: 'noWhitespace', options: { errorPath: 'value' } }],
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'type_start_time',
      type: 'radio',
      className: 'basis-6/12',
      defaultValue: 'SPECTIME',
      props: {
        label: 'Thi gian bt u',
        required: true,
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        options: campaignService.getListTimeStart().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'start_time',
      type: 'date',
      className: 'basis-6/12',
      props: {
        ShowTime: true,
        format: 'dd-MM-yyyy HH:mm:ss',
        placeholder: 'DD-MM-YYYY hh:mm:ss',
        required: true,
        emitTimeLocal: true,
        DisabledDate: (current: Date): boolean => {
          const today = new Date();
          today.setHours(0, 0, 0, 0);
          return current < today;
        },
        change: (field, event: any) => {
          const endField = field?.parent?.fieldGroup?.find((f: any) => f.key === 'end_time');
          if (endField) {
            const control = form.get('end_time') as FormControl;
            control.updateValueAndValidity();
          }
        },
      },
      hideExpression: (field: any) => {
        return field?.type_start_time
          ? field?.type_start_time !== 'SPECTIME'
          : true;
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      validators: {
        validation: [startTimeValidator],
      },
    },
    {
      key: 'type_end_time',
      type: 'radio',
      className: 'basis-6/12',
      defaultValue: 'SPECTIME',
      props: {
        label: 'Thi gian kt thc',
        required: true,
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        options: campaignService.getListTimeEnd().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'end_time',
      type: 'date',
      className: 'basis-6/12',
      props: {
        required: true,
        placeholder: 'DD-MM-YYYY hh:mm:ss',
        emitTimeLocal: true,
        ShowTime: true,
        format: 'dd-MM-yyyy HH:mm:ss',
        DisabledDate: (current: Date): boolean => {
          const today = new Date();
          today.setHours(0, 0, 0, 0);
          return current < today;
        },
        change: (field, event: any) => {
          const startField = field?.parent?.fieldGroup?.find(f => f.key === 'start_time');
          if (startField) {
            const control = form.get('start_time') as FormControl;
            control.updateValueAndValidity();
          }
        },
      },
      hideExpression: (field: any) => {
        return field?.type_end_time ? field?.type_end_time !== 'SPECTIME' : true;
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      expressionProperties: {
        'props.DisabledDate': (model: any, formState: any, field?: FormlyFieldConfig) => {
          const startValue = formState?.model?.start_time;
          if (startValue) {
            return (current: Date) => dayjs(current).isBefore(dayjs(startValue));
          }
          return (current: Date) => false;
        }
      },
      validators: {
        validation: [startTimeValidator],
      },
    },
    {
      key: 'description',
      type: 'quill-editor',
      className: 'basis-6/12',
      props: {
        label: 'Th l chng trnh',
        placeholder: 'Nhp th l',
        required: true,
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      fieldGroupClassName: 'w-full items-center mt-4',
      key: 'customer_group',
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      fieldGroup: [
        {
          type: 'array',
          key: 'config',
          expressions: {
            'props.disabled': 'formState.disabled',
          },
          props: {
            label: 'Khch hng',
            addText: 'Thm Khch hng',
          },

          fieldArray: {
            wrappers: ['group'],
            hideExpression: (field: any) => {
              if (
                field.operator === 'after_specific_day' ||
                field.operator === 'before_specific_day'
              ) {
                return true;
              }
              return false;
            },
            fieldGroup: [
              {
                key: 'type',
                type: 'select-custom',
                className: 'basis-6/12',
                props: {
                  label: 'Khch hng',
                  placeholder: 'Chn iu kin',
                  viewValue: 'name',
                  labelProp: 'name',
                  valueProp: 'id',
                  options: [
                    { name: 'H s khch hng ', id: 'customer_profile' },
                  ],
                },
              },
              {
                key: 'attribute',
                type: 'select-custom',
                props: {
                  placeholder: 'Chn iu kin',
                  viewValue: 'name',
                  labelProp: 'name',
                  valueProp: 'id',
                  options: [
                    {
                      name: 'Theo thi gian ng k mi',
                      id: 'thoi_gian_dang_ky',
                    },
                    {
                      name: 'Theo s in thoi',
                      id: 'phone',
                    },
                  ],
                  change: (field: any) => {
                    if (field.model && field?.model?.attribute === 'thoi_gian_dang_ky' && !field?.model?.operator) { 
                      field.model.operator = 'before_duration';
                      field.model.group = 'customer';
                    }
                    if (field.model && field?.model?.attribute === 'phone' && !field?.model?.operator) { 
                      field.model.operator = 'in';
                      field.model.group = 'customer';
                    }
                    field.formControl.markAsTouched();
                  }
                },
                hideExpression: (field: any) => {
                  if (!field?.type) {
                    return true;
                  }
                  return field?.type === 'customer_profile' ? false : true;
                }
              },
              {
                fieldGroupClassName: 'flex row items-center mt-4',
                key: '',
                hideExpression: (field: any) => {
                  if (!field?.attribute) {
                    return true;
                  }
                  return field?.attribute === 'thoi_gian_dang_ky' ? false : true;
                },
                fieldGroup: [
                  {
                    key: 'value',
                    type: 'nz-input-number',
                    props: {
                      placeholder: 'Nhp s',
                      type: 'number',
                      min: 0,
                    },
                  },
                  {
                    key: 'unit',
                    type: 'select-custom',
                    className: ' ml-4',
                    defaultValue: 'day',
                    props: {
                      viewValue: 'name',
                      labelProp: 'name',
                      valueProp: 'id',
                      required: true,
                      options: [
                        {
                          id: 'day',
                          name: 'Ngy',
                        },
                      ],
                    },
                  },
                ],
              },
              {
                key: 'value',
                hideExpression: (field: any) => {
                  console.log(field)
                  if (!field?.attribute) {
                    return true;
                  }
                  return field?.attribute === 'phone' ? false : true;
                },
                type: 'tags',
                props: {
                  // label: 'ajhi'
                }
              }
            ],
          },
        },
      ],
    },
  ];
};



function startTimeValidator(control: FormGroup): ValidationErrors | null {
  const startTime = control.parent?.value?.start_time;
  const endTime = control.parent?.value?.end_time;

  if (startTime && endTime && dayjs(endTime).isBefore(startTime)) {
    return { 'startTimeInvalid': true };
  }
  return null;
}
