import { JsonPipe, NgFor, NgIf } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, ViewContainerRef, inject } from '@angular/core';
import { FormGroup, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { NZ_MODAL_DATA, NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { IComLibsCampaignAddEditConditionModalComponent } from './modal/modal.component';
import { ConfigAllFormCondition } from '../defines/condition/condition.define';
import { NzDropDownModule } from 'ng-zorro-antd/dropdown';
import { NzSelectModule } from 'ng-zorro-antd/select';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IcomLibsCampaignConvertTooltipConditionPipe } from '../pipes/convert-tooltip-condition.pipe';
import { v4 as uuidv4 } from 'uuid';
@Component({
  selector: 'icom-libs-campaign-management-detail-mission-condition',
  templateUrl: './condition.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzStepsModule,
    IComLibsCampaignAddEditConditionModalComponent,
    NgFor,
    JsonPipe,
    NzDropDownModule,
    NzSelectModule,
    ReactiveFormsModule,
    FormsModule,
    IcomLibsCampaignConvertTooltipConditionPipe
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailMissionAddEditConditionComponent implements OnInit {
  // protected form: FormGroup = new FormGroup({});
  protected modelOrigin!: any;
  protected model: any;
  protected fields: FormlyFieldConfig[] = [];
  protected options: FormlyFormOptions = {
    formState: {
      disabled: false,
    },
  };

  protected modalService = inject(NzModalService);
  protected viewContainerRef = inject(ViewContainerRef);
  protected readonly nzModalData = inject(NZ_MODAL_DATA);
  private cdr = inject(ChangeDetectorRef);
  private campaignService = inject(IComLibsServicesProjectCampaignV3Service);

  @Input() dataCondition: any;
  @Input() dataMission: any;
  @Input() isAdd!: boolean;
  @Input() mode: 'view' | 'edit' | undefined;

  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();

  constructor() {
    this.model = {};
  }

  ngOnInit() {
    this.icomFunctionControl.emit({
      save: this.handleSave.bind(this)
    });
    if (this.dataCondition && !this.dataCondition.config) {
      this.dataCondition.config = [];
    }
    if (this.mode === 'view') {
      this.options.formState.disabled = true;
    }
    this.mapFormField();
  }

  private mapFormField() {
    const configAllFormCondition = ConfigAllFormCondition(this.campaignService);

    this.dataCondition.config.forEach((config: any, index: number) => {
      const result = configAllFormCondition.find(item => item.type === config.type);
      if (!result) {
        return;
      }
      this.dataCondition.config[index] = { ...config, ...result };
    });
  }

  protected handlerAddCondition(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    const modal: NzModalRef<IComLibsCampaignAddEditConditionModalComponent> = this.modalService.create<IComLibsCampaignAddEditConditionModalComponent>({
      nzTitle: 'Thm iu kin p dng',
      nzContent: IComLibsCampaignAddEditConditionModalComponent,
      nzMaskClosable: false,
      nzClosable: false,
      nzViewContainerRef: this.viewContainerRef,
      nzWidth: 968,
      nzFooter: [
        {
          label: 'ng',
          onClick: () => modal.destroy()
        },
        {
          label: 'Lu',
          type: 'primary',
          loading: false,
          onClick(): void {
            modal.getContentComponent().handlerSave();
            modal.destroy();
          }
        },
      ]
    });
    const instance = modal.getContentComponent();
    instance.icomSave.subscribe((result) => {
      this.dataCondition.config && this.dataCondition.config.push(...result);
      this.dataCondition.config = [...this.dataCondition.config];
      this.mapFormField();
      this.cdr.detectChanges();
    });
  }


  protected handleSave() {
    let error = false;
    this.dataCondition.config && this.dataCondition.config.forEach((config: any) => {
      if (!config?.form) {
        return;
      }
      if (config?.form && !config?.form?.valid) {
        Object.keys(config.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
          (config.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
        });
        error = true;

        return;
      }
      config.form.markAsTouched();
      (config.form as TYPE_FORM_CONTROL).submitted = true;
    })

    if (error) {
      return false;
    }

    if (!this.dataCondition?.config && !this.dataCondition?.config.length) {
      return;
    }
    this.dataCondition.relationship = '';

    // Fix tam cho BE: Nhiem vu cuoi
    if (this.dataMission && this.dataMission.click_action_param && this.dataMission.click_action_param === 'APP_SCREEN_WORKER_PROFILE') {
      !this.dataCondition.config.find((item: any) => item.id === 'uuid2') && this.dataCondition.config?.push(...[
        {
          "attribute": "full_name",
          "group": "customer",
          "id": "uuid2",
          "operator": "is_not_empty",
          "type": "customer_profile",
          "specific_hidden": true
        },
        {
          "attribute": "email",
          "group": "customer",
          "id": "uuid3",
          "operator": "is_not_empty",
          "type": "customer_profile",
          "specific_hidden": true
        },
        {
          "attribute": "birthdate",
          "group": "customer",
          "id": "uuid4",
          "operator": "is_not_empty",
          "type": "customer_profile",
          "specific_hidden": true
        },
        {
          "attribute": "address_city",
          "group": "customer",
          "id": "uuid5",
          "operator": "is_not_empty",
          "type": "customer_profile",
          "specific_hidden": true
        }
      ]);
    }

    // Fix tam cho BE: Nhiem vu phan thuong nguoi duoc moi

    const configReferenceRegister = this.dataCondition?.config?.find((item: any) => item.type === 'thoi_gian_dang_ky' && item.group === 'customer_reference');
    if(configReferenceRegister) {
      if(this.dataCondition?.config?.find((item: any) => item.operator === 'after_specific_day')) {
        this.dataCondition.config = this.dataCondition?.config.filter((item: any)  => item.operator !== 'after_specific_day')
      }
      if(this.dataCondition?.config?.find((item: any) => item.operator === 'before_specific_day')) {
        this.dataCondition.config = this.dataCondition?.config.filter((item: any)  => item.operator !== 'before_specific_day')
      }
      this.dataCondition.config.push(...this.convertObject(this.dataCondition?.config?.find((item: any) => item.type === 'thoi_gian_dang_ky')));
    }
    console.log(this.dataCondition);
    this.dataCondition.config.forEach((item: any, index: number) => {
      this.dataCondition.relationship += ` ${item.id} ${index !== this.dataCondition.config.length - 1 ? '&&' : ''}`;
      delete item.fields;
      delete item.form;
    });
    return true;
  }

  protected convertObject(originalObject: any) {
    const newObjects = [];

    if (originalObject.after_specific_day) {
      newObjects.push({
        operator: "after_specific_day",
        attribute: "thoi_gian_dang_ky",
        type: "customer_profile",
        "group": "customer_reference",
        "specific_hidden": true,
        value: originalObject.after_specific_day,
        id: uuidv4()
      });
    }
    if (originalObject.before_specific_day) {
      newObjects.push({
        operator: "before_specific_day",
        attribute: "thoi_gian_dang_ky",
        value: originalObject.before_specific_day,
        "group": "customer_reference",
        type: "customer_profile",
        id: uuidv4(),
        "specific_hidden": true
      });
    }
    return newObjects;
  }

  protected handlerRemove(e: Event, row: any) {
    e.stopPropagation();
    e.preventDefault();
    this.dataCondition.config = this.dataCondition.config?.filter((config: any) => config.id !== row.id);
    this.dataCondition.relationship = '';
    this.dataCondition?.config?.forEach((item: any) => {
      this.dataCondition.relationship += ` ${item.id} ${item.operator} `;
    });
    this.cdr.detectChanges();
  }
}
