import { markFormGroupTouched } from '@icom/defines';
import { NgIf } from '@angular/common';
import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output, inject } from '@angular/core';
import { FormArray, FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { NZ_MODAL_DATA } from 'ng-zorro-antd/modal';
import { FormlyConfigAddEditGeneralForm } from '../defines/general.define';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { TYPE_FORM_CONTROL } from '@icom/interfaces';

@Component({
  selector: 'icom-libs-campaign-management-detail-mission-information',
  templateUrl: './information.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzStepsModule
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailMissionAddEditInformationComponent implements OnInit {
  protected form: FormGroup = new FormGroup({});
  protected fields: FormlyFieldConfig[] = [];
  protected options: FormlyFormOptions = {
    formState: {
      disabled: false,
    },
  };

  @Input() mode: 'view' | 'edit' | undefined;
  @Input() modeExtend: 'view' | 'edit' | 'copy' | undefined;
  
  @Input() model: any;
  @Input() isAdd!: boolean;
  @Input() missions: any[] | undefined;

  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();

  protected readonly nzModalData = inject(NZ_MODAL_DATA);
  protected readonly iComLibsServicesProjectCampaignV3Service = inject(IComLibsServicesProjectCampaignV3Service);

  constructor() {
  }

  ngOnInit() {
    this.fields = FormlyConfigAddEditGeneralForm(
      this.iComLibsServicesProjectCampaignV3Service, this.missions, this.modeExtend);

    this.model = this.nzModalData.model;
    this.icomFunctionControl.emit({
      save: this.handleSave.bind(this)
    });
    if (this.mode === 'view') {
      this.options.formState.disabled = true;
    }
  }

  protected handleSave() {
    if (this.form.invalid) {
      markFormGroupTouched(this.form);
      return;
    }
    return true;
  }

  
}

