import { ValidationErrors, FormControl } from '@angular/forms';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { FormlyFieldConfig } from '@ngx-formly/core';
import {
  Observable,
  concatMap,
  distinctUntilChanged,
  map,
  startWith,
} from 'rxjs';


export function uniqueNameValidator(listMission?: any[], mode?: string): ValidationErrors | null  {
  let valueBeforeDirty = '';
  return (control: FormControl): ValidationErrors | null => {
    (!control.dirty && mode === 'edit') && (valueBeforeDirty = control.value);
    const namesList: any[] = [];
    listMission?.forEach((item: any) => {
      namesList.push(item.name);
    }); 
    if (namesList.filter((item) => control?.value?.trim() === item)?.length && valueBeforeDirty !== control?.value?.trim() ) {
      return { 'uniqueName': 'Tn ny  c s dng.' };
    }
    return null;
  };
}

export const FormlyConfigAddEditGeneralForm = (
  campaignService: IComLibsServicesProjectCampaignV3Service,
  existingNames?: any[],
  mode?: string
): FormlyFieldConfig[] => {
  return [
    {
      key: 'name',
      type: 'input',
      className: '',
      props: {
        placeholder: 'Nhp tn nhim v',
        label: 'Tn nhim v',
        type: 'string',
        required: true,
      },
      validators: {
        validation: [
          { name: 'noWhitespace', options: { errorPath: 'value' } },
          uniqueNameValidator(existingNames, mode)
        ],
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'avatar',
      type: 'upload',
      className: '',
      props: {
        label: 'Icon i din',
        size: 1024 * 100,
        required: true,
        labelType: 'Image',
        width: 200,
        height: 200,
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'type',
      type: 'select-custom',
      className: 'basis-6/12',
      props: {
        label: 'Loi nhim v',
        placeholder: 'Chn loi nhim v',
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        required: true,
        options: campaignService.getTypeMissions().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'info',
      type: 'quill-editor',
      className: 'basis-6/12',
      props: {
        label: 'Thng tin nhim v',
        placeholder: 'Nhp m t',
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'description',
      type: 'textarea',
      className: 'basis-6/12',
      props: {
        label: 'M t nhim v',
        placeholder: 'Nhp thng tin',
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'sponsor',
      props: {
        required: true,
        label: 'Nh ti tr',
        addText: 'Thm nh ti tr',
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      type: 'array',
      fieldArray: {
        fieldGroupClassName: 'flex row items-center',
        fieldGroup: [
          {
            key: 'id',
            type: 'select-custom',
            className: 'basis-8/12',
            props: {
              label: 'Nh ti tr',
              placeholder: 'Chn nh ti tr',
              viewValue: 'title',
              labelProp: 'title',
              valueProp: 'id',
              required: true,
              options: campaignService.getContributor().pipe(
                map((result) => {
                  return result.data;
                })
              ),
            },
          },
          {
            key: 'value',
            type: 'nz-input-number',
            className: 'basis-4/12 ml-4',
            props: {
              placeholder: 'Nhp t l ti tr',
              label: 'T l ti tr',
              type: 'number',
              min: 0,
              max: 100,
              required: true,
            },
          },
          {
            key: 'balance',
            type: 'input',
            className: 'basis-4/12 ml-4',
            props: {
              label: 'Ti khon k qu',
              disabled: true,
              required: true,
            },
            hooks: {
              onInit: (field) => {
                const form = field?.formControl?.parent;
                const fieldBControl = form?.get('id') as FormControl;

                fieldBControl.valueChanges.subscribe((selectedOptionValue) => {
                  const fieldBConfig = field?.parent?.fieldGroup?.find(
                    (f) => f.key === 'id'
                  );
                  const options = fieldBConfig?.props?.options;

                  let selectedOption = undefined;
                  if (options instanceof Observable) {
                    options.subscribe((data) => {
                      selectedOption = data.find(
                        (option) => option.id === selectedOptionValue
                      );
                      field?.formControl?.setValue(
                        selectedOption?.balance || 0
                      );
                    });
                  } else if (Array.isArray(options)) {
                    selectedOption = options.find(
                      (option) => option.value === selectedOptionValue
                    );
                    field?.formControl?.setValue(selectedOption?.balance || 0);
                  }
                });
              },
            },
          },
        ],
      },
    },
    {
      key: 'pool',
      type: 'select-custom',
      props: {
        label: 'Loi im thng Cp 1',
        type: 'text',
        placeholder: 'Cp 1 (Pool)',
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        required: true,
        options: campaignService.getPool().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'sub_pool',
      type: 'select-custom',
      props: {
        label: 'Loi im thng Cp 2',
        type: 'text',
        placeholder: 'Cp 2 (Pool)',
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        required: true,
        options: [],
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      hooks: {
        onInit: (field: any) => {
          const poolIdControl = field?.form?.get('pool');
          if (!poolIdControl) {
            return;
          }
          field.templateOptions.options = poolIdControl.valueChanges.pipe(
            startWith(poolIdControl.value),
            distinctUntilChanged(),
            concatMap((idPool: string) => {
              return campaignService.getSubPool({ pool_id: idPool }).pipe(
                map((result) => {
                  if (!result?.data?.find((option: any) => field?.model?.sub_pool === option.id)) {
                    field.formControl.setValue(null);
                  }
                  return result.data;
                })
              );
            })
          );
        },
      },
    },
    {
      key: 'action',
      type: 'select-custom',
      props: {
        label: 'Hnh ng thc hin nhim v',
        type: 'text',
        placeholder: ' Chn hnh ng',
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        required: true,
        options: campaignService.getActionMission().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'lock',
      type: 'checkbox',
      props: {
        label: 'Kha thc hin nhim v sau khi tham gia ',
        description: 'Khch hng ch cn xc nhn tham gia nhim v 1 ln  (v d n button Thc hin) th c th thc hin nhim v bt k lc no trong thi gian din ra chin dch m khng cn n li.'
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'click_action_type',
      type: 'select-custom',
      className: 'basis-6/12',
      props: {
        label: 'X l khi n nt thc hin nhim v',
        placeholder: 'Chn x l',
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
        options: campaignService.getListActionType().pipe(
          map((result) => {
            return result.data;
          })
        ),
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      hooks: {
        onInit: (field) => {
          const form = field.formControl?.parent;
          const control = form?.get('click_action_type');
          if (control) {
            control.valueChanges
              .pipe(startWith(control.value), distinctUntilChanged())
              .subscribe((selectedId) => {
                if (field?.props?.options instanceof Observable) {
                  field?.props?.options.subscribe((data) => {
                    const selectedOption = data.find(
                      (option) => option.id === selectedId
                    );
                    // Cp nht field `specific_type_click_action_selected` da trn loi ca option  chn
                    const typeField = form?.get('specific_type_click_action_selected');
                    if (typeField) {
                      if (selectedOption?.type === 'input') {
                        typeField.setValue('input');
                      } else {
                        typeField.setValue('select');
                      }
                    }
                  });
                }
              });
          }
        },
      },
    },
    {
      key: 'specific_type_click_action_selected',
      defaultValue: '',
      className: 'hidden',
      type: 'input'
    },
    {
      key: 'click_action_param',
      hideExpression: (model, formState, field) =>
        field?.parent?.model.specific_type_click_action_selected !== 'input',
      type: 'input',
      className: 'basis-6/12',
      props: {
        label: 'Gi tr tham s',
        placeholder: 'Nhp gi tr',
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
    },
    {
      key: 'click_action_param',
      type: 'select-custom',
      className: 'basis-6/12',
      hideExpression: (model, formState, field) =>
        field?.parent?.model.specific_type_click_action_selected === 'input',
      props: {
        label: 'Tham s',
        placeholder: 'Chn tham s',
        options: [],
        viewValue: 'title',
        labelProp: 'title',
        valueProp: 'id',
      },
      expressions: {
        'props.disabled': 'formState.disabled',
      },
      hooks: {
        onInit: (field: any) => {
          const poolIdControl = field?.form?.get('click_action_type');
          if (!poolIdControl) {
            return;
          }
          field.templateOptions.options = poolIdControl.valueChanges.pipe(
            startWith(poolIdControl.value),
            distinctUntilChanged(),
            concatMap((idPool: string) => {
           
              return campaignService.getListActionParam(idPool).pipe(
                map((result) => {
                  if (!result.data.find((option: any) => field?.model?.click_action_param === option.id)) {
                    field.formControl.setValue(null);
                  }
                  return result.data;
                })
              );
            })
          );
        },
      },
    },
  ];
};
