import { JsonPipe, NgFor, NgForOf, NgIf } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, OnInit, Output, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { distinctUntilChanged, map, startWith } from 'rxjs/operators';
import { NzListModule } from 'ng-zorro-antd/list';
import { NzTagModule } from 'ng-zorro-antd/tag';
import { v4 as uuidv4 } from 'uuid';
import { ListFormula1, ListFormula2 } from '../../defines/condition/condition.define';
@Component({
  selector: 'icom-libs-campaign-management-detail-mission-condition-modal',
  templateUrl: './modal.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzStepsModule,
    NzListModule, JsonPipe,
    NgFor,
    NzTagModule, NgForOf
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class IComLibsCampaignAddEditConditionModalComponent implements OnInit {
  protected form: FormGroup = new FormGroup({});
  protected modelOrigin!: any;
  protected fields: FormlyFieldConfig[] = [];
  protected options: FormlyFormOptions = {
    formState: {
      disabled: true,
    },
  };
  protected model: any;
  protected listItemSelected: any;
  protected listFormula: any[] = [];
  protected listFormulaSelected: any[] = [];
  
  @Output() icomSave: EventEmitter<any[]> = new EventEmitter<any[]>();

  private cdr = inject(ChangeDetectorRef);

  constructor() {
    this.model = {};
    this.fields = [
      {
        key: 'group',
        type: 'select-custom',
        defaultValue: 'customer',
        props: {
          label: 'Tiu ch cp 1',
          viewValue: 'name',
          labelProp: 'name',
          valueProp: 'id',
          options: ListFormula1()
        },
      },
      {
        key: 'sub_group',
        type: 'select-custom',
        props: {
          label: 'Tiu ch cp 2',
          viewValue: 'name',
          labelProp: 'name',
          valueProp: 'id',
          options: []
        },
        hooks: {
          onInit: (field: any) => {
            const teams: any = ListFormula2;

            const sportControl = field?.parent?.get?.('group').formControl;
            field.props.options = sportControl?.valueChanges.pipe(
              startWith(sportControl.value),
              distinctUntilChanged(),
              map(() => {
                const sportId = sportControl.value;
                const options = teams.filter((team: any) => team.groupId === sportId);
                if (!options.find((option: any) => sportId === option.id)) {
                  field.formControl.setValue(null);
                }
                const items = options as any[];
                if (options && items as any[] && items.length) {
                  this.listItemSelected = items || [];
                }
                return options;
              }),
            );
          },
        },
      }
    ]
  }

  ngOnInit() {
    setTimeout(() => {
      (this.form.controls as any)['sub_group']?.valueChanges.subscribe({
        next: (keySubGroup: any) => {
          let list: any[] = [];
          this.listItemSelected.forEach((groupTwo: any) => {
            const items = [groupTwo].find((item: any) => item.id === keySubGroup)?.items
            if(items && items.length) {
              list = items;
            }
          });
          this.listFormula = list;
          this.cdr.detectChanges();
        }
      });
    }, 200);
  }

  protected handlerClick(e: Event, itemAdd: any) {
    e.stopPropagation();
    e.preventDefault();
    if(this.listFormulaSelected && this.listFormulaSelected.length && this.listFormulaSelected.find(item  => item.type === itemAdd.type)) {
      return;
    }
    this.listFormulaSelected.push(itemAdd);
    this.cdr.detectChanges();
  }

  protected trackByFn(index: any, user: any) {
    return user.id;
  }

  public handlerSave() {
    this.icomSave.emit(this.listFormulaSelected.map((item) => {
      return {
        ...item, 
        id: item.id ||  uuidv4()
      }
    }));
  }
}
