import { NgIf } from '@angular/common';
import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ComponentRef, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { FormlyConfigAddEditGeneralForm } from './defines/general.define';
import { NZ_MODAL_DATA } from 'ng-zorro-antd/modal';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { cloneDeep } from 'lodash';
import { IComLibsCampaignManagementDetailMissionAddEditInformationComponent } from './information/information.component';
import { IComLibsCampaignManagementDetailMissionAddEditRewardComponent } from './reward/reward.component';
import { IComLibsCampaignManagementDetailMissionAddEditConditionComponent } from './condition/condition.component';

@Component({
  selector: 'icom-libs-campaign-management-detail-mission-add_edit',
  templateUrl: './add-edit.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzStepsModule,
    NzSpaceModule,
    IComLibsCampaignManagementDetailMissionAddEditRewardComponent,
    IComLibsCampaignManagementDetailMissionAddEditInformationComponent,
    IComLibsCampaignManagementDetailMissionAddEditConditionComponent
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailMissionAddEditComponent implements OnInit, AfterViewInit {
  protected current: number;
  protected modelOrigin!: any;
  protected eventFunctionControl: any;

  public readonly nzModalData = inject(NZ_MODAL_DATA);
  private readonly cdr = inject(ChangeDetectorRef);

  @Input() model: any;
  @Input() isAdd!: boolean;
  @Input() modal!: any;

  @ViewChild('customFooter', { static: false }) templateFooter!: TemplateRef<{}>;

  @Output() icomSave: EventEmitter<any> = new EventEmitter<any>();

  constructor() {
    this.current = 0;
  }

  ngOnInit() {
    this.model = this.nzModalData.model;

    if(this.model && !this.model.condition) {
      this.model.condition = {};
    }
    if(this.model && !this.model.reward) {
      this.model.reward = {};
    }
    if(this.model && !this.model.reward_reference) {
      this.model.reward_reference = {};
    }
    
  }

  ngAfterViewInit() {
    const modal = this.modal;
    modal.updateConfig({
      nzFooter: this.templateFooter
    });
    modal.containerInstance.cdr.detectChanges();
    this.cdr.detectChanges();
  }

  protected handlerNextStep(e: Event) {
    e.stopPropagation();
    e.preventDefault();

    if (this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
      this.current++;
      this.cdr.detectChanges();
      return;
    }
    if(this.nzModalData.mode === 'view') {
      this.current++;
      this.cdr.detectChanges();
    }
  }

  protected handlerFunctionControl(event: any){
    this.eventFunctionControl = event;
  }

  protected handlerPrev(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.current--;
    this.cdr.detectChanges();
  }

  protected handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    
    if (this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
      this.icomSave.emit(this.model);
      this.cdr.detectChanges();
    }

  }

  protected handlerClose(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    
      this.icomSave.emit();
      this.cdr.detectChanges();
  }

  protected handlerIndexChange(index: number): void {
    if(this.current === index) {
      return;
    }
    if(this.nzModalData.mode === 'view' && (this.current === index + 1 || this.current === index - 1) ) {
      this.current = index;
      return;
    }
    if (this.nzModalData.mode === 'edit' && this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
      this.current += 1;
    }
  }
}
