import { ChangeDetectionStrategy, ChangeDetectorRef, Component, DestroyRef, OnInit, inject } from '@angular/core';
import { CommonModule, NgIf } from '@angular/common';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { NzCardModule } from 'ng-zorro-antd/card';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { IComLibsCampaignManagementDetailGeneralComponent } from './general/general.component';
import { IComLibsCampaignManagementDetailMissionComponent } from './mission/mission.component';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { HttpErrorResponse } from '@angular/common/http';
import { IComMessageService } from '@icom/services/common';
import { cloneDeep } from 'lodash';
import { lastValueFrom } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { NzModalService } from 'ng-zorro-antd/modal';
import { IComLibsCampaignManagementDetailLayoutComponent } from './layout/layout.component';
import { v4 as uuidv4 } from 'uuid';
import { mergeObjects } from '@icom/defines';
import { NzSpinModule } from 'ng-zorro-antd/spin';
@Component({
  selector: 'icom-libs-campaign-management-detail',
  standalone: true,
  imports: [
    CommonModule,
    NzTypographyModule,
    NzPageHeaderModule,
    NzSpaceModule,
    NzTabsModule,
    NzIconModule,
    NzCardModule,
    NgIf,
    NzStepsModule,
    NzButtonModule,
    IComLibsCampaignManagementDetailGeneralComponent,
    IComLibsCampaignManagementDetailMissionComponent,
    IComLibsCampaignManagementDetailLayoutComponent,
    NzSpinModule
  ],
  templateUrl: './detail.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailComponent implements OnInit {
  protected current!: number;
  protected dataCampaign: any;
  protected dataCampaignOrigin: any;
  protected eventFunctionControl: any;
  protected isLoading!: boolean;
  protected isLoadingFirst!: boolean;
  protected mode: 'view' | 'edit';

  protected campaignService = inject(IComLibsServicesProjectCampaignV3Service);
  protected IComMessageService = inject(IComMessageService);
  protected router = inject(Router);
  protected destroyRef = inject(DestroyRef);
  protected route = inject(ActivatedRoute);
  protected cdr = inject(ChangeDetectorRef);
  protected modalService = inject(NzModalService);

  constructor() {
    this.current = 0;
    this.dataCampaign = {
      config_mission: {},
      config_layout: {}
    };
    this.mode = 'view';
    this.isLoadingFirst = true;
  }

  ngOnInit() {
    this.route.queryParams.pipe(takeUntilDestroyed(this.destroyRef)).subscribe((params) => {
      if (!params) {
        return;
      }
      const id = params['id'];
      this.mode = params['mode'];
      this.isLoadingFirst = true;
      this.cdr.detectChanges();
      this.campaignService.detail({ id: +id }).subscribe((response: any) => {
        this.dataCampaign = response.data;
        this.dataCampaignOrigin = cloneDeep(this.dataCampaign);
        this.isLoadingFirst = false;
        this.cdr.detectChanges();
      }, (err: any) => {
        this.isLoadingFirst = false;
        this.cdr.detectChanges();
      });
    })
  };

  protected pre(): void {
    this.current -= 1;
  }

  protected next(): void {
    if (this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
      this.current += 1;
    }
    if (this.mode === 'view') {
      this.current += 1;
    }
  }

  protected handlerIndexChange(index: number): void {
    if (this.current === index) {
      return;
    }
    if (this.mode === 'view' && (this.current === index + 1 || this.current === index - 1)) {
      this.current = index;
      return;
    }
    if (this.mode === 'edit' && this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
      this.current += 1;
    }
  }

  protected handlerFunctionControl(event: any) {
    this.eventFunctionControl = event;
  }

  protected async handlerSave(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    if (this.eventFunctionControl && this.eventFunctionControl.save && !this.eventFunctionControl.save()) {
      return;
    }
    this.modalService.confirm({
      nzTitle: 'Bn c mun lu chin dch?',
      nzContent: `<b style="color: red;">${this.dataCampaign.name}</b>`,
      nzOkText: 'Lu',
      nzOkType: 'primary',
      nzMaskClosable: false,
      nzClosable: true,
      nzOnOk: this.processSubmit.bind(this),
      nzCancelText: 'ng'
    }, 'info');
  }

  private trimFormFields() {
    const formValues = this.dataCampaign as any;
    Object.keys(formValues).forEach(key => {
      if (typeof formValues[key] === 'string') {
        formValues[key] = formValues[key].trim();
      }
    });
  }

  private async processSubmit() {
    try {
      this.isLoading = true;
      this.trimFormFields();
      (!this.dataCampaign.status || this.dataCampaign.status === 'DRAFT') && (this.dataCampaign.status = 'INACTIVE');
      if (this.eventFunctionControl && this.eventFunctionControl.save && this.eventFunctionControl.save()) {
        this.addSpecificGeneral();
        await lastValueFrom(this.campaignService.addEditCampaign(this.dataCampaign));
        this.IComMessageService.showAlertMessageDone(!this.dataCampaign?.id ? 'To mi chin dch thnh cng.' : 'Sa chin dch thnh cng.');
        this.router.navigateByUrl(`campaign/management`);
        this.dataCampaignOrigin = cloneDeep(this.dataCampaign);
      }
    } catch (err) {
      console.log(err);
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Sa chin dch tht bi');
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected async handlerSaveDraft(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    try {
      if (!this.dataCampaign.name) {
        this.IComMessageService.showAlertMessageError('Vui lng nhp tn chin dch');
        return;
      }
      this.trimFormFields();
      this.isLoading = true;
      this.dataCampaign.status = 'DRAFT';
      this.addSpecificGeneral();
      const result = await lastValueFrom(this.campaignService.addEditCampaign(this.dataCampaign));
      if (result && result.data) {
        mergeObjects(result.data, this.dataCampaign);
      }
      this.IComMessageService.showAlertMessageDone('Lu nhp chin dch thnh cng');
      this.dataCampaignOrigin = cloneDeep(this.dataCampaign);
    } catch (err) {
      console.log(err);
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Sa chin dch tht bi');
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerCancel(e?: Event) {
    e?.stopPropagation();
    e?.preventDefault();
    if(this.mode ==='view') {
      this.router.navigateByUrl(`campaign/management`);
      return;
    }
    this.modalService.confirm({
      nzTitle: !this.dataCampaign?.id ? 'Bn c mun hy thm mi chin dch khng?' : 'Bn c hy b thao tc ?',
      nzOkText: 'ng ',
      nzOkType: 'primary',
      nzMaskClosable: false,
      nzClosable: true,
      nzOnOk: () => this.router.navigateByUrl(`campaign/management`),
      nzCancelText: 'ng'
    }, 'info');
  }

  protected addSpecificGeneral() {
    const objectAdd = [
      {
        "id": "uuid2",
        "group": "customer",
        "type": "customer_profile",
        "value": this.dataCampaign.start_time,
        "attribute": "thoi_gian_dang_ky",
        "operator": "after_specific_day"
      },
      {
        "id": "uuid3",
        "group": "customer",
        "type": "customer_profile",
        "value": this.dataCampaign.end_time,
        "attribute": "thoi_gian_dang_ky",
        "operator": "before_specific_day"
      }
    ];
    objectAdd.forEach((copyItem: any) => {
      const foundItem = this.dataCampaign?.customer_group?.config?.find((gocItem: any) => gocItem.id === copyItem.id);
      if (foundItem) {
        Object.assign(foundItem, copyItem);
      } else {
        this.dataCampaign?.customer_group?.config?.push(copyItem);
      }
    });

    this.dataCampaign.customer_group && (this.dataCampaign.customer_group.relationship = '');
    if (this.dataCampaign?.customer_group?.config && this.dataCampaign?.customer_group?.config?.length) {
      const config = this.dataCampaign?.customer_group?.config;
      config.forEach((item: any) => {
        item.id = item.id || uuidv4()
      });
      config?.forEach((item: any, index: number) => {
        this.dataCampaign.customer_group.relationship += ` ${item.id} ${index !== config.length - 1 ? '&&' : ''}`;
      });
    }

    if (this.dataCampaign?.config_mission?.missions && this.dataCampaign?.config_mission?.missions?.length) {
      const config = this.dataCampaign?.config_mission?.missions;
      config.forEach((item: any, index: number) => {
        item.id = index + 1;
      });
    }
  }
}
