import { NgIf } from '@angular/common';
import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  inject,
} from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import {
  FormlyFieldConfig,
  FormlyFormOptions,
  FormlyModule,
} from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzStepsModule } from 'ng-zorro-antd/steps';
import { FormlyConfigAddEditRewardForm } from '../defines/reward.define';
import {
  IComLibsServicesProductService,
  IComLibsServicesProjectCampaignV3Service,
} from '@icom/services/projects';
import { TYPE_FORM_CONTROL } from '@icom/interfaces';
import { markFormGroupTouched } from '@icom/defines';
import { IComMessageService } from '@icom/services/common';

@Component({
  selector: 'icom-libs-campaign-management-detail-mission-reward',
  templateUrl: './reward.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzStepsModule,
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailMissionAddEditRewardComponent
  implements OnInit {
  protected form: FormGroup = new FormGroup({});
  protected modelOrigin!: any;
  protected fields: FormlyFieldConfig[] = [];
  protected options!: FormlyFormOptions;

  protected readonly campaignService = inject(
    IComLibsServicesProjectCampaignV3Service
  );
  protected readonly productService = inject(IComLibsServicesProductService);
  protected readonly icomMessageService = inject(IComMessageService);

  @Input() mode: 'view' | 'edit' | undefined;
  @Input() isAdd!: boolean;
  @Input() data: any;
  @Input() model: any;

  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();

  constructor() {

  }

  ngOnInit() {
    this.fields = FormlyConfigAddEditRewardForm(
      this.campaignService,
      this.productService,
      this.data
    );
    this.options = {
      formState: {
        disabled: false,
        required: false,
        mainModel: this.model,
      },
    };
    this.icomFunctionControl.emit({
      save: this.handleSave.bind(this),
    });
    if (this.mode === 'view') {
      this.options.formState.disabled = true;
    }
  }

  protected handleSave() {
    if (this.form.invalid) {
      markFormGroupTouched(this.form);
      return;
    }
    const listVoucher = this.model?.reward?.items?.find((item: any) => item.type === 'voucher');
    const listVoucherRef = this.model?.reward_reference?.items?.find((item: any) => item.type === 'voucher');
    if(this.model?.reward?.type === 'random' && listVoucher && this.model?.reward?.type_value > listVoucher?.id?.length) {
      this.icomMessageService.showAlertMessageError('Vi hnh thc thng ngu nhin, s phn thng cho mi khch hng phi nh hn hoc bng s voucher  chn.');
      return;
    }
    if(this.model?.reward?.type === 'random' && this.model?.reward?.items?.find((item: any) => item.type === 'point')) {
      this.icomMessageService.showAlertMessageError('Hnh thc thng ngu nhin ch p dng cho loi phn thng voucher.');
      return;
    }
    if(this.model?.reward?.type === 'random' && listVoucherRef && this.model?.reward_reference?.type_value > listVoucherRef?.id?.length) {
      this.icomMessageService.showAlertMessageError('Vi hnh thc thng ngu nhin, s phn thng cho mi khch hng phi nh hn hoc bng s voucher  chn.');
      return;
    }
    if(this.model?.reward?.type === 'random' && this.model?.reward_reference?.type === 'random' && this.model?.reward_reference?.items?.find((item: any) => item.type === 'point')) {
      this.icomMessageService.showAlertMessageError('Hnh thc thng ngu nhin ch p dng cho loi phn thng voucher.');
      return;
    }
    return true;
  }
}
