import { DecimalPipe, NgFor, NgIf } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, ViewContainerRef, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzFormModule } from 'ng-zorro-antd/form';
import { IComLibsCampaignManagementDetailMissionAddEditComponent } from './add-edit/add-edit.component';
import { ConfigFormlyMissionOrder } from '../defines/mission.define';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { NzEmptyModule } from 'ng-zorro-antd/empty';
import { NzDescriptionsModule } from 'ng-zorro-antd/descriptions';
import { v4 as uuidv4 } from 'uuid';
import { cloneDeep, filter } from 'lodash';
import { TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { combineLatest } from 'rxjs';
import { IcomCampaignDetailMissionParseValue } from './add-edit/pipes/parse-value.pipe';
import { IComMessageService } from '@icom/services/common';
import { NzStatisticModule } from 'ng-zorro-antd/statistic';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzIconModule } from 'ng-zorro-antd/icon';
@Component({
  selector: 'icom-libs-campaign-management-detail-mission',
  templateUrl: './mission.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzEmptyModule,
    IComLibsCampaignManagementDetailMissionAddEditComponent,
    NzDescriptionsModule,
    NgFor,
    IcomCampaignDetailMissionParseValue,
    NzStatisticModule,
    NzPageHeaderModule,
    DecimalPipe,
    NzIconModule
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementDetailMissionComponent {
  protected form: FormGroup = new FormGroup({});
  protected fields: FormlyFieldConfig[] = [];
  protected options: FormlyFormOptions = {
    formState: {
      disabled: false,
    },
  };
  protected isLoading!: boolean;
  protected listDataActionMission: any[];
  protected listDataContributor: any[];

  protected modalService = inject(NzModalService);
  protected viewContainerRef = inject(ViewContainerRef);
  protected cdr = inject(ChangeDetectorRef);
  protected campaignService = inject(IComLibsServicesProjectCampaignV3Service);
  protected IComMessageService = inject(IComMessageService);
  
  @Input() data: any | undefined;
  @Input() mode: 'view' | 'edit' | undefined;

  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();

  constructor() {
    this.listDataActionMission = [];
    this.listDataContributor = [];
    this.fields = ConfigFormlyMissionOrder(this.campaignService);
  }

  ngOnInit() {
    this.icomFunctionControl.emit({
      save: this.handleSave.bind(this)
    });

    if(this.mode === 'view') {
      this.options.formState.disabled = true;
    }

    combineLatest([
      this.campaignService.getActionMission(),
      this.campaignService.getContributor()
    ]
    ).subscribe((results: any) => {
      const [dataActionMission, dataContributor] = results;
      this.listDataActionMission = dataActionMission?.data;
      this.listDataContributor = dataContributor?.data;
      this.cdr.detectChanges();
    });
  }

  protected handlerAddNew(e: Event, data?: any, view?: 'edit' | 'view' | 'copy') {
    e.stopPropagation();
    e.preventDefault();
    const modal: NzModalRef<IComLibsCampaignManagementDetailMissionAddEditComponent> = this.modalService.create<IComLibsCampaignManagementDetailMissionAddEditComponent>({
      nzTitle: data && view !== 'copy' ? `Sa nhim v: ${data.name}` : 'Thm nhim v',
      nzContent: IComLibsCampaignManagementDetailMissionAddEditComponent,
      nzMaskClosable: false,
      nzKeyboard: false,
      nzClosable: true,
      nzViewContainerRef: this.viewContainerRef,
      nzData: {
        model: cloneDeep(data) || {},
        isAdd: true,
        mode: view === 'copy' ? 'edit' : view,
        missions: this.data.missions || [],
        modeExtend: view
      },
      nzWidth: 968,
      nzFooter: []
    });
    const instance = modal.getContentComponent();
    instance.icomSave.subscribe((result) => {
      if(!result) {
        modal.close();
        this.cdr.detectChanges();
        return;
      }
      if(!this.data?.missions || !this.data?.missions?.length) {
        this.data.missions = [];
      }
      if (!result.id) {
        this.data.missions?.push({ ...result, id: uuidv4() });
      } else {
        this.mergeObjects(data, result);
      }
      modal.close();
      this.cdr.detectChanges();
    });
    instance.modal = modal;
  }

  protected handleEditMission(e: Event, data: any, view: 'edit' | 'view') {
    e.stopPropagation();
    e.preventDefault();
    this.handlerAddNew(e, data, view);

  }

  protected mergeObjects(obj1: any, obj2: any) {
    for (const key in obj2) {
      if (obj2.hasOwnProperty(key)) {
        if (obj1.hasOwnProperty(key)) {
          obj1[key] = obj2[key];
        } else {
          obj1[key] = obj2[key];
        }
      }
    }
  }

  protected handlerRemove(e: Event, row: any) {
    e.stopPropagation();
    this.modalService.confirm({
      nzTitle: `Bn c chc chn mun xo nhim v ${row.name} khng ?`,
      nzOkText: 'C',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzMaskClosable: false,
      nzClosable: true,
      nzOnOk: this.processDeleteMission.bind(this, row),
      nzCancelText: 'Khng'
    }, 'error');
  }

  protected handlerCopy(e: Event, row: any) {
    e.stopPropagation();
    this.handlerAddNew(e, {
      ...row,
      id: '', 
    }, 'copy')
  }
  

  protected handleSave(): boolean {
    if (!this.form.valid) {
      Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
        (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
      });
      return false;
    }
    this.form.markAsTouched();
    (this.form as TYPE_FORM_CONTROL).submitted = true;
    if(!this.data?.missions?.length) {
      this.IComMessageService.showAlertMessageError('Bn cha chn nhim v.');
      return false;
    }
    return true;
  }

  protected processDeleteMission(row: any) {
    this.data.missions = this.data?.missions?.filter((mission: any) => mission.id !== row.id);
    this.cdr.detectChanges();
  }
  
}