import { Pipe, PipeTransform } from '@angular/core';
import { ListFormula1, ListFormula2 } from '../defines/condition/condition.define';

@Pipe({
  name: 'icomLibsCampaignConvertTooltipConditionPipe',
  standalone: true
})
export class IcomLibsCampaignConvertTooltipConditionPipe implements PipeTransform {

  transform(dataCondition: any): any {
    // return null;
    let string = '';
    if(dataCondition.group) {
      string += ListFormula1().find(item => item.id === dataCondition.group)?.name || '';
    }
    if(dataCondition.type) {
      string += ' - '
      string += ListFormula2.find(item => item.groupId === dataCondition.group)?.name || '';
    }
    return string;
  }

}
